import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, Router } from '@angular/router';
import { AuthConstants } from 'src/app/configuration/auth-constants';
import { IAuditLog } from 'src/app/models/IAuditLog';
import { IUser } from 'src/app/models/IUser';
import { HttpService } from 'src/app/services/http.service';
import { StorageService } from 'src/app/services/storage.service';

export interface IAuditLogString {
    user: string;
    eventType: string;
    object: string;
    fieldName: string;
    newValue: string;
    previousValue: string;
    batchName: string;
    createdAt: string;
}
@Component({
  selector: 'app-audit-trail',
  templateUrl: './audit-trail.page.html',
  styleUrls: ['./audit-trail.page.scss'],
})
export class AuditTrailPage implements OnInit {

  objectDictionary: string[] = [
    'OT',
    'Lote',
    'Producto',
    'Usuario',
    'Configuracin'
  ];

  eventTypeDictionary: string[] = [
    'Modificado',
    'Creado',
    'Borrado',
    'Abierto',
    'Cerrado',
    'Login',
    'Logout'
  ]

  displayedColumns: string[] = [
    'createdAt', 
    'user', 
    'eventType', 
    'object', 
    'fieldName', 
    'newValue', 
    'previousValue', 
    'batchName'
  ];

  logs: IAuditLog[];
  logsString: IAuditLogString[];
  user: IUser;
  token: string;
  
  constructor(
    private router: Router,
    private activatedRoute: ActivatedRoute,
    private httpClient: HttpService,
    private storageService: StorageService,
  ) { }

  ngOnInit() {
    this.storageService.get(AuthConstants.USER).then(res=>{
      this.user = res.user;
      this.token = res.accessToken;
    });

    this.getAuditList();
  }

  getAuditList(){
    this.httpClient.getAuditTrail(this.token, 0, 11)
    .subscribe(logsList => {
      console.log(logsList);
      this.logs = logsList;
      this.logsString = logsList.map( log => {
        return {...log, eventType: this.eventTypeDictionary[log.eventType], object: this.objectDictionary[log.object], createdAt: this.buildDatetimeString(log.createdAt)}
      });
      console.log(this.logsString );
    }, error => console.log(error))
  }

 buildDatetimeString(input_date: string){
    let date = new Date(input_date);
    return date.getFullYear() + '/' + this.addZeroBefore(date.getMonth() + 1) + '/' + this.addZeroBefore(date.getDate()) + ' ' + this.addZeroBefore(date.getHours()) + ':' + this.addZeroBefore(date.getMinutes()) + ':' + this.addZeroBefore(date.getSeconds());
  }

  addZeroBefore(num: number){
    let num_string = ';'
    if(num < 10){
      num_string = 0 + num.toString();
    } else {
      num_string = num.toString();
    }
    return num_string;
  }

  getBackButtonText() {
    const win = window as any;
    const mode = win && win.Ionic && win.Ionic.mode;
    return mode === 'ios' ? 'Atrs' : 'Atrs';
  }

}
